<template>
  <div class="snowflake">
    <div>111❄️</div>
    <canvas width="200" height="300" id="cvs1"></canvas>
  </div>
</template>

<script>
export default {
  name: "snowflake",
  data() {
    return {
      msg: "Welcome to Your Vue.js App"
    };
  },
  created() {},
  mounted() {
    // setInterval(()=>{

    //     },5000)
    this.init();
    // this.random()
  },
  methods: {
    init() {
      // this.test1();

      // for(let i=0;i<20;i++){
      let d = this.random();
      this.test2(d);
      // }
    },
    test2(m) {
      let ctx = document.getElementById("cvs1").getContext("2d"),
        i = 10,
        j = 1;
      //
      ctx.save();
      // do {
      setInterval(() => {
        ctx.clearRect(0,0,200,300)
        i++;
        j -= j / 200;
        ctx.globalAlpha = j;
        ctx.font = "20px Georgia";
        ctx.fillText("❄️", m, i);

        if (i > 330) {
          i = 10;
          j = 1;
        }
      }, 20);
      ctx.restore();
      // } while (i > 300);
    },
    random() {
      return parseInt(Math.random() * 10) * 15;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
#cvs1 {
  /* background: pink; */
  border: 1px solid #ccc;
}
</style>



