<template>
  <div class="hello">
    <h1>{{ msg.slice(1)}}</h1>
    <h2>Essential Links</h2>
    <canvas width="200" height="300" id="cvs"></canvas>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  data() {
    return {
      msg: "Welcome to Your Vue.js App"
    };
  },
  created() {
    // map,foreach
    // this.init();
  },
  mounted() {
    setInterval(()=>{
      this.init();

    },5000)
    // this.random()
  },
  methods: {
    init() {
      // this.test1();
      
        this.test2();
      
    },
    test1() {
      let arr = [
        { name: "111", age: 11 },
        { name: "22", age: 8 },
        { name: "33", age: 18 }
      ];
      console.log("map");
      let a = arr.map((item, index) => {
        item["sex"] = "F";
        return item;
      });
      console.log(arr);
      console.log(a);
    },
    test2() {
      let ctx = document.getElementById("cvs").getContext("2d");
      ctx.clearRect(0,0,200,300)
      for (let i = 0; i < 100; i++) {
      ctx.beginPath();
      ctx.fillStyle = "rgba(255,0,0,0.5)";
      ctx.arc(
        this.random() * 2,
        this.random() * 3,
        this.random() / 5,
        0,
        2 * Math.PI
      );
      // ctx.stroke();
      ctx.fill();
      }
    },
    random() {
      return parseInt(Math.random() * 10) * 10;
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
#cvs {
  /* background: pink; */
  border: 1px solid #ccc;
}
</style>



